import React, { useState, useEffect } from 'react';

// Components
import Logo from './components/Logo';
import Searchbar from './components/Searchbar';
import Results from './components/Results';

import geoApi from './api/geo-api';

import './App.css';

function App() {
  const [searchQuery, setSearchQuery] = useState('');
  const [results, setResults] = useState([]); //'Enter IP and press ‚ÄúSearch‚Äù to get geolocation data'
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchGeolocation();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchQuery]);

  const fetchGeolocation = async () => {
    try {
      //   –≤—ã–≤–æ–¥–∏–º –≤ –æ—Ç–¥–µ–ª—å–Ω—É—é –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é  searchQuery –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –ø–µ—Ä–µ–¥–∞—Ç—å options –≤ props –≤ weather-api.js;
      const options = { searchQuery, error };

      //   —Å–æ—Ç–æ—è–Ω–∏–µ –∑–∞–≥—Ä—É–∑–∫–∏, –º–µ–Ω—è–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ
      setIsLoading(true);

      //–ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ —Ç–æ, –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–∏—á–µ–≥–æ –Ω–µ –≤–≤–µ–ª –≤ input, –Ω–µ –æ—Ç–ø—Ä–∞–≤–ª—è—Ç—å http-–∑–∞–ø—Ä–æ—Å
      if (!searchQuery) {
        return;
      }

      // –≤—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ –∏–∑ —Ñ–∞–π–ª–∞ –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–æ–ø–∏—Å—ã–≤–∞–µ—Ç –ª–æ–≥–∏–∫—É –Ω–∞—Å—Ç—Ä–æ–µ–∫ Api (geo-api.js)
      const response = await geoApi.fetchGeolocation(options);
      console.log(response);

      setResults([...results, response]);

      if (!response) {
        throw new Error('Something went wrong üòû. Please try again.');
      }
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
  };

  const changeQuery = query => {
    setSearchQuery(query);
    setResults([]);
    setError(null);
  };

  return (
    <>
      <div className="App">
        <Logo />
        <Searchbar onSubmit={changeQuery} />

        {results.length > 0 ? (
          <Results results={results} />
        ) : (
          <p>Enter IP and press ‚ÄúSearch‚Äù to get geolocation data</p>
        )}

        {error && <p className="error-msg">{error.message}</p>}
      </div>
    </>
  );
}

export default App;
